
import ModalBase from "@/components/modal/ModalBase";
import VueNextSelect from "vue-next-select";
import "vue-next-select/dist/index.min.css";
import {Axios} from "@/assets/js/http-common";

export default {
  name: "ModalRegister",
  components: {
    ModalBase,
    VueNextSelect,
  },
  props: {
    openModalLogin: Function,
    groups: Array,
  },
  data() {
    return {
      username: "",
      usernameError: "",
      email: "",
      emailError: "",
      password: "",
      passwordError: "",
      group: null,
      groupError: "",
      touchTime: 0,
    };
  },
  methods: {
    open() {
      return this.$refs.modalBase.open();
    },
    close() {
      return this.$refs.modalBase.close();
    },
    regexValidation(re, str) {
      return re.test(str);
    },
    usernameValidation() {
      if (!this.regexValidation(/^[\w \-А-я_]{1,128}$/, this.username)) {
        this.usernameError = "Некорректное имя пользователя";
        return false;
      }
      this.usernameError = "";
      return true;
    },
    passwordValidation() {
      if (!this.regexValidation(/(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z!@#$%^&*]{6,}/, this.password)) {
        this.passwordError = "Слабый пароль";
        return false;
      }
      this.passwordError = "";
      return true;
    },
    groupValidation() {
      if (!this.group || !Object.entries(this.group).length) {
        this.groupError = "Группа обязательное поле";
        return false;
      }
      this.groupError = "";
      return true;
    },
    emailValidation() {
      if (!this.regexValidation(/^([\wА-я.-]+)@([\w.-]+)\.([A-z]{1,10})$/, this.email)) {
        this.emailError = "Некорректный E-mail";
        return false;
      }
      this.emailError = "";
      return true;
    },
    confirm() {
      if (!this.isValidForm) return;
      return this.$refs.modalBase.confirm({
        username: this.username,
        email: this.email,
        group: this.group?.id,
        password: this.password,
      });
    },
    async showError() {
      return new Promise((resolve) => {
        this.$refs.modalBase.showError();
        setTimeout(() => {
          this.$refs.modalBase.hideError();
          resolve();
        }, 1000);
      });
    },
    async showSuccess() {
      return new Promise((resolve) => {
        this.username = "";
        this.email = "";
        this.password = "";
        this.group = {};
        this.$refs.modalBase.hideSuccess();
        this.$refs.modalBase.close();
        resolve();
      });
    },
    checkExistsUsername() {
      this.touchTime = new Date().getTime();
      setTimeout(async () => {
        if (this.touchTime <= new Date().getTime() - 600 && this.touchTime > 0) {
          try {
            const { data } = await Axios.get(`users/?username=${this.username}`);
            if (data?.is_exists) {
              this.usernameError = "Это имя уже занято";
            }
          } catch (error) {
            console.error(error);
          }
        }
      }, 600);
    },
  },
  computed: {
    isValidForm() {
      this.usernameValidation();
      this.passwordValidation();
      this.emailValidation();
      this.groupValidation();
      return !(
        this.usernameError.length ||
        this.passwordError.length ||
        this.emailError.length ||
        this.groupError.length
      );
    },
  },
};
